<script>
import AboutPage from './AboutPage.vue'
import EducationPage from './EducationPage.vue'
//import PublicationsPage from "./PublicationsPage.vue";
import TalkPage from './TalkPage.vue'
import MiscPage from './MiscPage.vue'
export default {
  name: 'MyIndex',
  components: {
    AboutPage,
    EducationPage,
    TalkPage,
    //PublicationsPage,
    MiscPage,
  },
  data() {
    return {
      mouseOut: null,
      mouseIn: null,
      currentMenu: 'home',
    }
  },
  watch: {},
  mounted() {
    this.mouseOut = this.$refs.mouseOut
    this.mouseIn = this.$refs.mouseIn
    document.addEventListener('mousemove', this.mouseMoveHandler)
    window.addEventListener('scroll', this.handleScroll)
  },
  beforeUnmount() {
    document.removeEventListener('mousemove', this.mouseMoveHandler)
    window.removeEventListener('scroll', this.handleScroll)
  },
  methods: {
    setActiveMenu(menu) {
      this.currentMenu = menu

      const element = document.getElementById(menu)
      if (element) {
        const offset = 0
        const elementPosition = element.getBoundingClientRect().top
        const offsetPosition = elementPosition + window.pageYOffset - offset

        window.scrollTo({
          top: offsetPosition,
          behavior: 'smooth',
        })
        this.scrollContainer = document.querySelector('.right')
        this.scrollContainer.scrollTo({
          top: element.offsetTop,
          behavior: 'smooth',
        })
      }
    },
    mouseMoveHandler(event) {
      const x = event.clientX
      const y = event.clientY

      // Áõ¥Êé•‰ΩøÁî® clientX Âíå clientYÔºåÂõ†‰∏∫‰ΩøÁî®‰∫Ü position: fixed
      this.mouseOut.style.transform = `translate(${x}px, ${y}px)`
      this.mouseIn.style.transform = `translate(${x}px, ${y}px)`
    },
    handleScroll() {
      const sections = [
        'home',
        'about',
        'education',
        'talk',
        //"publications",
        'misc',
      ]

      for (const section of sections) {
        const element = document.getElementById(section)
        if (element) {
          const rect = element.getBoundingClientRect()
          if (
            rect.top <= window.innerHeight / 3 &&
            rect.bottom >= window.innerHeight / 3
          ) {
            this.currentMenu = section
            break
          }
        }
      }
    },
  },
}
</script>

